<template>
    <div class="dialog">
        <el-dialog :title="dialog.title" :visible.sync="dialog.show" :close-on-click-modal="false" :model-append-to-body="false">
            <div class="form">
                <el-form ref="form" :model="formData" :rules="form_rules" label-width="120px" style="margin:10px;width:auto;">
                    <el-form-item label="收支类型：">
                        <el-select v-model="formData.type" placeholder="收支类型">
                            <el-option v-for="(formtype, index) in format_type_list" :key="index" :value="formtype" :label="formtype"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="收支描述:" prop="desc">
                        <el-input v-model="formData.desc" autocomplete="off" placeholder="收支描述"></el-input>
                    </el-form-item>
                    <el-form-item label="收入:" prop="income">
                        <el-input v-model="formData.income" autocomplete="off" placeholder="收入"></el-input>
                    </el-form-item>
                    <el-form-item label="支出:" prop="expend">
                        <el-input v-model="formData.expend" autocomplete="off" placeholder="支出"></el-input>
                    </el-form-item>
                    <el-form-item label="账户现金:" prop="cash">
                        <el-input v-model="formData.cash" autocomplete="off" placeholder="账户现金"></el-input>
                    </el-form-item>
                    <el-form-item label="备注:" prop="remark">
                        <el-input v-model="formData.remark" autocomplete="off" placeholder="备注"></el-input>
                    </el-form-item>
                    <el-form-item class="text_right">
                        <el-button @click="dialog.show = false">取消</el-button>
                        <el-button type="primary" @click="onSubmit('form')">提交</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "nomal-dialog",
    props: {
        dialog: Object,
        formData: Object
    },
    data() {
        return {
            format_type_list: [
                "提现",
                "提现手续费",
                "充值",
                "优惠券",
                "充值礼券",
                "转账"
            ],
            form_rules: {
                desc: [
                    { required: true, message: '收支描述不能为空', trigger: 'blur' }
                ],
                income: [
                    { required: true, message: '收入不能为空', trigger: 'blur' }
                ],
                expend: [
                    { required: true, message: '支出不能为空', trigger: 'blur' }
                ],
                cash: [
                    { required: true, message: '账户现金不能为空', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        onSubmit(form) {
            this.$refs[form].validate(valid => {
                if(valid) {
                    const url = this.dialog.option === 'add' ? 'add' : `edit/${this.formData.id}`
                    this.$axios.post(`/api/profiles/${url}`, this.formData)
                        .then(res => {
                            // 添加成功
                            this.$message({
                                message: '数据添加成功',
                                type: 'success'
                            })
                            this.dialog.show = false
                            this.$emit('update')
                        })
                }
            })
        }
    }
}
</script>

